import {
  Dialog,
  DialogContent,
  
  Typography,
  Button,
  CircularProgress,
} from "@mui/material";
import { makeStyles } from "@mui/styles";

import React, { useLayoutEffect, useRef } from "react";
import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useHistory } from "react-router";
import {
  confirmWithDraw,
  setWithDrawDataConfirm,
} from "../../../../redux/reducers/withdraw";

const ConformDialog = ({
  t,
  confirm,
  setConfirm,
  sum,
  chosenNetwork,
  open,
  setOpen,
  commission,
}) => {
  const classes = useStyles();
  const dispatch = useDispatch();
  const history = useHistory();

  const [loading, setLoading] = useState(false);

  const { withDrawDataConfirm, withDrawData } = useSelector(
    ({ withdrawReducer }) => withdrawReducer
  );

  const firstUpdate = useRef(true);

  useLayoutEffect(() => {
    if (firstUpdate.current) {
      firstUpdate.current = false;
      return;
    }

    if (withDrawDataConfirm?.message?.security) {
      setOpen(true);
      setConfirm(false);
      setLoading(false);
    }

    if (withDrawDataConfirm.severity === 200) {
      setConfirm(false);
      setLoading(false);
      setTimeout(() => {
        history.push("/dashboard/history");
        dispatch(
          setWithDrawDataConfirm({
            ...withDrawDataConfirm,
            severity: null,
            open: false,
          })
        );
      }, 1000);
    }

    // eslint-disable-next-line
  }, [withDrawDataConfirm]);

  return (
    <Dialog
      open={confirm}
      onClose={() => setConfirm(false)}
      scroll="body"
      classes={{ paper: classes.paper }}
    >
      <DialogContent className={classes.DialogContent}>
        <Typography variant="h2" className={classes.title}>
          {t("paymentСonfirmation")}
        </Typography>
        <ul style={{ listStyle: "none" }}>
          <Typography variant="h5" component="li">
            {t("AmountToBeDebited")}
            <div></div>
            <span>
              {withDrawData?.message?.confirm?.amount}{" "}
              {withDrawData?.message?.confirm?.currency}
            </span>
          </Typography>
          <Typography variant="h5" component="li">
            {t("AmountReceivable")}
            <div></div>
            <span>
              {sum} {withDrawData?.message?.confirm?.currency}
            </span>
          </Typography>
          <Typography variant="h5" component="li">
            {t("Commission")}
            <div></div>
            <span>
              {withDrawData?.message?.confirm?.commission}{" "}
              {withDrawData?.message?.confirm?.currency}
            </span>
          </Typography>
          <Typography variant="h5" component="li">
            {t("OurCommission")}
            <div></div>
            <span>
              {commission} {withDrawData?.message?.confirm?.currency}
            </span>
          </Typography>
          <Typography variant="h5" component="li">
            {t("NetworkCommission")}
            <div></div>
            <span>
              {chosenNetwork?.commission}{" "}
              {withDrawData?.message?.confirm?.currency}
            </span>
          </Typography>
          <Typography variant="h5" component="li">
            {t("note")}:<div></div>
            <span>{withDrawData?.message?.confirm?.description}</span>
          </Typography>
        </ul>
        <Button
          variant="contained"
          color="primary"
          disabled={loading}
          onClick={() => {
            setLoading(true);
            dispatch(confirmWithDraw(withDrawData?.message?.confirm?.id));
          }}
        >
          {loading ? (
            <CircularProgress className="progress" color="secondary" />
          ) : (
            t("confirm")
          )}
        </Button>
      </DialogContent>
    </Dialog>
  );
};

export default ConformDialog;

const useStyles = makeStyles((theme) => ({
  paper: {
    borderRadius: 16,
    width: "100%",
  },

  DialogContent: {
    padding: "40px 31px!important",
    display: "flex",
    justifyContent: "center",
    flexDirection: "column",

    [theme.breakpoints.down(992)]: {
      padding: "16px",
    },

    "& ul": {
      "& li": {
        fontWeight: 300,
        marginBottom: 16,
        display: "flex",
        justifyContent: "space-between",
        whiteSpace: "nowrap",
        alignItems: "baseline",

        "& div": {
          width: "100%",
          height: "fit-content",
          borderBottom: "2px dotted #B8C0CC",
          margin: "0 3px",
        },

        "&:last-child": {
          "& span": {
            textOverflow: "ellipsis",
            overflow: "hidden",
          },
        },
        "& span": {
          fontWeight: 500,
          maxWidth: "50%",
        },
      },
    },

    "& button": {
      width: "100%",
      height: 56,
      marginTop: 16,
      background: "#FCE34D",
      borderRadius: "8px",
      border: "none",
      fontSize: 18,
      fontWeight: 500,
    },
  },

  close: {
    position: "absolute",
    top: 0,
    right: 0,
  },

  title: {
    textAlign: "center",
    color: "#000",
    fontWeight: 500,
    letterSpacing: "-0.04em",
    lineHeight: "56px",
    marginBottom: 24,

    [theme.breakpoints.down(992)]: {
      fontSize: 48,
    },

    [theme.breakpoints.down(768)]: {
      fontSize: 34,
      lineHeight: "40px",
    },
  },

  subtitle: {
    color: "#64748B",
    marginLeft: 10,
  },
}));
