import React, { useEffect, useLayoutEffect, useRef, useState } from "react";
import {
  Box,
  Button,
  CircularProgress,
  Container,
  FormControl,
  Grid,
  InputLabel,
  MenuItem,
  Select,
  Snackbar,
  TextField,
  Typography,
  useMediaQuery,
  useTheme,
} from "@mui/material";
import { makeStyles } from "@mui/styles";
import warning from "../../../../assets/images/state/warning.svg";
import CommissionIcon from "../../../../assets/images/state/commissionDark.svg";
import CoinIcon from "../../../../assets/images/state/coin.svg";
import { MediumTitle } from "../../../../shared/Typography";
import { Redirect, withRouter } from "react-router";
import {
  createWithDraw,
  getWithDrawParameters,
  setWithDrawData,
  setWithDrawDataConfirm,
} from "../../../../redux/reducers/withdraw";
import { useDispatch, useSelector } from "react-redux";
import { useFormik } from "formik";
import { Alert } from "@mui/material";
import ConformDialog from "./ConfirmDialog";
import NotFound from "../../../NotFound";
import F2aDialog from "../../../../components/2faDialog";

const WithdrawalDetails = ({ t, match }) => {
  const classes = useStyles();
  const theme = useTheme();
  const mobile = useMediaQuery(theme.breakpoints.down(768));
  const dispatch = useDispatch();

  const [choosenNetwork, setChoosenNetwork] = useState(null);
  const [sum, setSum] = useState("");
  const [commission, setCommission] = useState("");
  const [open, setOpen] = useState(false);
  const [confirm, setConfirm] = useState(false);

  const firstUpdate = useRef(true);

  const { СurrenciesList } = useSelector(
    ({ СurrenciesReducer }) => СurrenciesReducer
  );
  const {
    withDrawParameters,
    networks,
    withDrawDataConfirm,
    withDrawData,
    isLoading,
  } = useSelector(({ withdrawReducer }) => withdrawReducer);

  const withdrawCoin = СurrenciesList.filter(
    (item) => item.currency === match.params.coin
  )[0];

  const formik = useFormik({
    initialValues: {
      sum: "",
      currency: "",
      wallet: "",
      network: "",
    },
    onSubmit: (values) => {
      values.currency = match.params.coin;
      dispatch(createWithDraw(values));
    },
  });

  useEffect(() => {
    dispatch(getWithDrawParameters(match.params.coin));
    // eslint-disable-next-line
  }, []);

  // useEffect(() => {
  //   if (networks) {
  //     formik.setFieldValue("network", networks[0]?.network);
  //   }
  //   // eslint-disable-next-line
  // }, [withDrawParameters?.data?.parameters]);

  useEffect(() => {
    const choosenNetwork = networks?.find(
      (e) => e.network == formik.values.network
    );

    const commission =
      (formik.values.sum / 100) *
      withDrawParameters?.data?.parameters?.commission;

    setCommission(commission);

    setSum(formik.values.sum - commission - choosenNetwork?.commission);

    setChoosenNetwork(choosenNetwork);
    // eslint-disable-next-line
  }, [formik.values.sum, formik.values.network]);

  useLayoutEffect(() => {
    if (firstUpdate.current) {
      firstUpdate.current = false;
      return;
    }

    if (withDrawData?.message?.confirm) {
      setConfirm(true);
    }
    // eslint-disable-next-line
  }, [withDrawData]);

  return (
    <>
      {withDrawParameters.status === 400 ? (
        <NotFound />
      ) : withDrawParameters.status === 403 ? (
        <Redirect exact from="/" to="/dashboard/settings/verification" />
      ) : (
        <section style={{ color: "#000" }}>
          <ConformDialog
            t={t}
            setConfirm={setConfirm}
            confirm={confirm}
            sum={sum}
            commission={commission}
            chosenNetwork={choosenNetwork}
            open={open}
            setOpen={setOpen}
          />
          <F2aDialog open={open} setOpen={setOpen} t={t} />
          <Snackbar
            open={withDrawData?.open || withDrawDataConfirm?.open}
            autoHideDuration={3000}
            onClose={() => {
              if (withDrawData?.open)
                dispatch(setWithDrawData({ ...withDrawData, open: false }));
              else
                dispatch(
                  setWithDrawDataConfirm({
                    ...withDrawDataConfirm,
                    open: false,
                  })
                );
            }}
            anchorOrigin={{ vertical: "top", horizontal: "center" }}
          >
            <Alert
              severity={
                withDrawDataConfirm?.severity === 200 ? "success" : "error"
              }
              color={
                withDrawDataConfirm?.severity === 200 ? "success" : "error"
              }
              variant="filled"
            >
              {withDrawData?.message?.messages ||
                withDrawDataConfirm?.message?.messages}
            </Alert>
          </Snackbar>
          {isLoading ? (
            <Box
              display="flex"
              justifyContent="center"
              alignItems="center"
              height="80vh"
            >
              <CircularProgress color="secondary" />
            </Box>
          ) : (
            <Container>
              <Typography
                variant="h3"
                className={classes.WithdrawalDetailsTitle}
              >
                {t("WithdrawFundsFrom")} {match.params.coin}{" "}
                {t("withdrawWallet")}
              </Typography>
              {networks?.length < 2 && (
                <Box className={classes.boxMb}>
                  <img src={warning} style={{ marginRight: 16 }} alt="" />
                  <Typography
                    variant="h5"
                    style={{ color: "#F59E0B", fontWeight: "300" }}
                  >
                    Внимание! Вывод {match.params.coin} возможен только на{" "}
                    {choosenNetwork?.network}
                  </Typography>
                </Box>
              )}
              <Grid container spacing={2} justifyContent="center">
                <Grid xs={12} sm={6} md={3} item>
                  <Box
                    className={classes.whitebox}
                    style={{ height: 210, flexDirection: "column" }}
                  >
                    <img className="icon-lg" src={CoinIcon} alt="" />
                    <Typography variant="h5" className={classes.subtitle}>
                      {t("MinAmount")}
                    </Typography>
                    <Typography variant="h4" className={classes.title}>
                      {!choosenNetwork ? "-" : choosenNetwork?.min_qty}
                      &#160;
                      {match.params.coin}
                    </Typography>
                  </Box>
                </Grid>
                <Grid xs={12} sm={6} md={3} item>
                  <Box
                    className={classes.whitebox}
                    style={{ height: 210, flexDirection: "column" }}
                  >
                    <img className="icon-lg" src={CommissionIcon} alt="" />
                    <Typography variant="h5" className={classes.subtitle}>
                      {t("OurCommission")}
                    </Typography>
                    <Typography variant="h4" className={classes.title}>
                      {withDrawParameters?.data?.parameters?.commission} %
                    </Typography>
                  </Box>
                </Grid>
                <Grid xs={12} sm={6} md={3} item>
                  <Box
                    className={classes.whitebox}
                    style={{ height: 210, flexDirection: "column" }}
                  >
                    <img className="icon-lg" src={CommissionIcon} alt="" />
                    <Typography variant="h5" className={classes.subtitle}>
                      {t("NetworkCommission")}
                    </Typography>
                    <Typography variant="h4" className={classes.title}>
                      {!choosenNetwork ? "-" : choosenNetwork?.commission}
                      &#160;
                      {match.params.coin}
                    </Typography>
                  </Box>
                </Grid>
              </Grid>
              <form onSubmit={formik.handleSubmit}>
                <Box mt={"40px"}>
                  <Box display="flex" justifyContent="flex-end">
                    <MediumTitle
                      variant="h4"
                      className={classes.BuyDetailsTitle}
                      onClick={() => {
                        formik.setFieldValue("sum", withdrawCoin?.balance);
                      }}
                    >
                      {t("availableAmount")}{" "}
                      <span>{withdrawCoin?.balance}</span> {match.params.coin}
                    </MediumTitle>
                  </Box>
                  <Box
                    display="flex"
                    alignItems="start"
                    mb={2}
                    style={mobile ? { flexDirection: "column" } : null}
                    sx={{
                      flexDirection: { xs: "column", sm: "column", md: "row" },
                    }}
                  >
                    <FormControl fullWidth className={classes.formControl}>
                      <InputLabel>{t("network")}</InputLabel>
                      <Select
                        name="network"
                        required
                        label={t("network")}
                        variant="outlined"
                        value={formik.values.network}
                        onChange={formik.handleChange}
                      >
                        {networks?.map((item, idx) => (
                          <MenuItem key={idx} value={item.network}>
                            {item.name}
                          </MenuItem>
                        ))}
                      </Select>
                    </FormControl>
                    <Box
                      display="flex"
                      width={"100%"}
                      sx={{ margin: { xs: "16px 0 0 0", md: "0 16px" } }}
                    >
                      <Box className={classes.WithdrawalDetailsLabel}>
                        {match.params.coin}
                      </Box>
                      <TextField
                        label={t("AmountDispatch")}
                        name="sum"
                        fullWidth
                        required
                        type="text"
                        error={
                          (formik.values.sum &&
                            formik.values.sum < +choosenNetwork?.min_qty) ||
                          formik.values.sum > +choosenNetwork?.max_qty
                        }
                        helperText={
                          (formik.values.sum &&
                            formik.values.sum < +choosenNetwork?.min_qty) ||
                          formik.values.sum > +choosenNetwork?.max_qty
                            ? `мин-${+choosenNetwork?.min_qty}/макс-${+choosenNetwork?.max_qty}`
                            : null
                        }
                        InputProps={{
                          inputProps: {
                            pattern: "\\d*.\\d*",
                          },
                        }}
                        value={formik.values.sum}
                        onChange={formik.handleChange}
                        onKeyPress={(event) => {
                          if (!/[0-9.]/.test(event.key)) {
                            event.preventDefault();
                          }
                        }}
                        variant="outlined"
                        className={classes.WithdrawalDetailsInput}
                      />
                    </Box>
                    <Box
                      display="flex"
                      alignItems="start"
                      width={"100%"}
                      sx={{ margin: { xs: "16px 0 0 0", md: "0 16px" } }}
                    >
                      <Box className={classes.WithdrawalDetailsLabel}>
                        {match.params.coin}
                      </Box>
                      <TextField
                        fullWidth
                        label={t("AmountReceivable")}
                        name="sum"
                        disabled
                        value={!formik.values.sum ? "" : sum}
                        onChange={formik.handleChange}
                        onBlur={formik.handleChange}
                        variant="outlined"
                        className={classes.WithdrawalDetailsInput}
                      />
                    </Box>
                  </Box>
                </Box>
                <Box mt={"24px"}>
                  <MediumTitle style={{ color: "#323A46" }}>
                    {t("number")} {match.params.coin} {t("withdrawWallet")}
                  </MediumTitle>
                  <TextField
                    fullWidth
                    label={`${t("enterNumber")} ${match.params.coin} ${t(
                      "withdrawWallet"
                    )}`}
                    style={{
                      backgroundColor: "#fff",
                      borderRadius: 8,
                      marginTop: 16,
                    }}
                    required
                    name="wallet"
                    value={formik.values.wallet}
                    onChange={formik.handleChange}
                    variant="outlined"
                  />
                </Box>
                <Button
                  variant="contained"
                  size="large"
                  type="submit"
                  disabled={
                    !formik.values.sum ||
                    !formik.values.wallet ||
                    (formik.values.sum &&
                      formik.values.sum < +choosenNetwork?.min_qty) ||
                    formik.values.sum > +choosenNetwork?.max_qty
                  }
                  color="primary"
                  className={classes.button}
                >
                  {t("Continue")}
                </Button>
              </form>
            </Container>
          )}
        </section>
      )}
    </>
  );
};

export default withRouter(WithdrawalDetails);

